import React from 'react';
import { Switch, Select } from 'antd';
import BG from '../../bg/bg';

const { Option } = Select;

class QuickTranslate extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      rightMenuSwitch: false,
      selectButton: false,
      engine: 'proxy',
    }
  }
  componentDidMount() {
    BG.getProperty('quickTranslate').then(res => {
      if (res.data) {
        this.setState(res.data)
      }
    })
  }
  setProperty(k, v) {
    let state = {}
    state[k] = v
    this.setState(state)
    BG.setProperty({
      'quickTranslate': state,
    })
  }
  onRightMenuSwitchChange(checked) {
    this.setProperty("rightMenuSwitch", !!checked)
  }
  onQuickTranslateUseEngineChange(val) {
    this.setProperty("engine", val)
  }
  onSelectButtonDisplayChange(checked) {
    this.setProperty("selectButton", !!checked)
  }
  render() {
    return (
      <div>
        <div className="label">划词翻译设置</div>
        <div className="block">
          <div className="options-list">
            <div className="option-item">
              <div className="main">
                <div className="subject">在右键菜单显示快捷方式</div>
              </div>
              <div className="operator">
                <Switch checked={this.state.rightMenuSwitch} onChange={this.onRightMenuSwitchChange.bind(this)} />
              </div>
            </div>
            <div className="option-item">
              <div className="main">
                <div className="subject">划词快捷按钮</div>
              </div>
              <div className="operator">
                <Switch checked={this.state.selectButton} onChange={this.onSelectButtonDisplayChange.bind(this)} />
              </div>
            </div>
            <div className="option-item">
              <div className="main">
                <div className="subject">划词翻译引擎设置</div>
              </div>
              <div className="operator">
                <Select value={this.state.engine} onChange={this.onQuickTranslateUseEngineChange.bind(this)}>
                  <Option value="proxy">聚合翻译引擎</Option>
                  <Option value="baidu">百度翻译引擎</Option>
                </Select>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default QuickTranslate