import React from 'react';

import BG from '../../bg/bg';

import './License.css';

class License extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      proxyLicense: {},
      proxyActive: false,
      baiduActive: false,
    }
  }
  componentDidMount() {
    BG.findProxyCurrentLicense().then(res => {
      if (res.result === 'ok' && res.data) {
        this.setState({proxyLicense: res.data, proxyActive: true})
      } else {
        this.setState({proxyLicense: '未找到可用的授权信息'})
      }
      return BG.findBaiduCurrentLicense()
    }).then(res => {
      if (res) {
        this.setState({baiduActive: true})
      }
    })
  }
  renderStatus(status) {
    const s = {
      'pending': {t: '未激活', c: 'gray'},
      'active': {t: '已激活', c: '#3367D6'},
      'disable': {t: '禁用', c: 'red'},
    }
    const sp = s[status] || {}
    const renderStyle = {}
    if (sp) {
      renderStyle['color'] = sp.c
    } else {
      renderStyle['color'] = 'gray'
    }
    return <span style={renderStyle}>{sp.t || '未知状态'}</span>
  }
  render() {
    return (
      <div>
        {
          !this.state.proxyActive && !this.state.baiduActive &&
          <div className="block">
            <div className="options-list">
              <div className="option-item">
                <div className="main">
                  <div className="subject">目前没有可用的授权信息</div>
                </div>
              </div>
            </div>
          </div>
        }
        { this.state.proxyActive &&
        <div>
          <div className="label">聚合翻译授权信息</div>
          <div className="block">
            <div className="options-list">
              <div className="option-item">
                <div className="main">
                  <div className="subject">授权名称</div>
                </div>
                <div className="operator display">{
                  this.state.proxyLicense.pkg_snapshot &&
                  this.state.proxyLicense.pkg_snapshot.name
                }</div>
              </div>
              <div className="option-item">
                <div className="main">
                  <div className="subject">授权状态</div>
                </div>
                <div className="operator display">{
                  this.renderStatus(this.state.proxyLicense.status)
                }</div>
              </div>
              <div className="option-item">
                <div className="main">
                  <div className="subject">授权到期时间</div>
                </div>
                <div className="operator display">{
                  new Date(this.state.proxyLicense.end_at * 1000 || 0).toLocaleString()
                }</div>
              </div>
              <div className="option-item">
                <div className="main">
                  <div className="subject">备注信息</div>
                </div>
                <div className="operator display">{
                  this.state.proxyLicense.description || '无'
                }</div>
              </div>
            </div>
          </div>
        </div>
        }
        { this.state.baiduActive &&
        <div>
          <div className="label">百度翻译授权信息</div>
          <div className="block">
            <div className="options-list">
            <div className="option-item">
                <div className="main">
                  <div className="subject">已激活，具体信息请参考百度翻译API控制台</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        }
      </div>
    )
  }
}

export default License